.fade {
  position: relative;
  width: 100%;
  min-height: 15vh;
  top: -50px;
  background-image: linear-gradient(0deg, transparent, black 75%);
  z-index: 1;
}

.star-wars {
  display: flex;
  justify-content: center;
  position: relative;
  height: 100vh;
  color: white;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 500%;
  font-weight: 600;
  letter-spacing: 6px;
  line-height: 150%;
  perspective: 400px;
  text-align: justify;
  overflow:hidden;
 
}
#home{
  background-image: url("https://images.unsplash.com/photo-1520034475321-cbe63696469a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
  box-shadow: inset 0px 10px 300px 100px black;
}

.crawl {
  position: relative;
  /* top: 99999px; */
  top:-800px;
  transform-origin: 50% 100%;
  animation: crawl 80s infinite linear;
  height: 800px;

  /* animation-iteration-count: infinite; */

}
.text-css{
  color:white;

}
.crawl > .title {
  font-size: 90%;
  text-align: center;
  /* animation-iteration-count: infinite; */


}

.crawl > .title h1 {
  margin: 0 0 100px;
  text-transform: uppercase;
  /* animation-iteration-count: infinite; */

}

@keyframes crawl {
0% {
    top: 10px;
    transform: rotateX(20deg)  translateZ(0);
  }
100% { 
    top: -6000px;
    transform: rotateX(25deg) translateZ(-2500px);
  }
}
